import * as React from "react";
import Select from "react-select";
import { useNavigate, useLocation } from "react-router";
import { shallowEqual, useSelector } from "react-redux";
import {getAllHints} from "reduxware/redux/hintsReducer";

interface Props {
    getValues: any;
    fetchImages: Function;
}

const SelectSection = (props: Props) => {
    const {getValues, fetchImages } = props;
    const hints = useSelector(getAllHints, shallowEqual);
    const history = useNavigate();
    const location = useLocation();
    const path = location.pathname;

    return hints?.length && path === "/" ? (
        <Select
            className="select-top"
            id="Select-Section-Unsplash"
            value={getValues}
            isClearable={true}
            menuIsOpen={true}
            onChange={selectValue => {
                fetchImages(selectValue.value);
                history("./images");
            }}
            options={hints}
        />
    ) : null;
};

export default SelectSection;
